import {IComments} from './interface';

function filterCommentsByParentId(comments: IComments[]) {
  const commentMap = new Map();

  // Đầu tiên, chúng ta tạo một Map với key là commentId
  comments.forEach(comment => {
    const parentId = comment.commentId === null ? 'root' : comment.commentId;
    if (!commentMap.has(parentId)) {
      commentMap.set(parentId, []);
    }
    commentMap.get(parentId).push(comment);
  });

  return commentMap;
}

const encodeEmojiToUnicode = (str: string): string => {
  return str
    .replace(/[\uD800-\uDBFF][\uDC00-\uDFFF]/g, match => {
      const codePoint =
        (match.charCodeAt(0) - 0xd800) * 0x400 +
        (match.charCodeAt(1) - 0xdc00) +
        0x10000;
      return '\\u{' + codePoint.toString(16).toUpperCase() + '}';
    })
    .replace(/[\u0080-\uD7FF\uE000-\uFFFF]/g, match => {
      return (
        '\\u' + match.charCodeAt(0).toString(16).toUpperCase().padStart(4, '0')
      );
    });
};

const decodeUnicodeToEmoji = (str: string): string => {
  return str
    .replace(/\\u\{([0-9A-F]{1,6})\}/g, (match, p1) => {
      return String.fromCodePoint(parseInt(p1, 16));
    })
    .replace(/\\u([0-9A-F]{4})/g, (match, p1) => {
      return String.fromCharCode(parseInt(p1, 16));
    });
};

export {filterCommentsByParentId, encodeEmojiToUnicode, decodeUnicodeToEmoji};
