import {Image, TouchableOpacity, View} from 'react-native';
import {FILE_ITEM_STATUS, IDetailFile} from '../../interface';
import TextCM from '../../../../../components/Text';
import {Divider} from '@ui-kitten/components';
import {Font} from '../../../../../../constants';
import {IconDocx} from '../../../../../../assets/images';

type Props = {
  data: IDetailFile;
};

const ListItemField = ({data}: Props) => {
  const getSize = (size: number) => {
    if (size && size >= 1024) {
      return Math.floor(size / 1024) + ' KB';
    } else if (size && size < 1024) {
      return size + ' MB';
    } else {
      return '-';
    }
  };

  const getStatus = (status: any) => {
    return 'status-collabo.' + Object.keys(FILE_ITEM_STATUS)[status];
  };
  return (
    <TouchableOpacity
      style={{
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: '#fff',
        paddingVertical: 8,
      }}>
      <View
        style={{
          width: 80,
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        {/* <Image width={32} height={32} source={{uri: data?.thumbnailLink}} /> */}
        <IconDocx width={32} height={32} />
      </View>
      <View
        style={{
          flex: 1,
          rowGap: 8,
        }}>
        <TextCM
          style={{
            fontSize: 14,
            color: '#00204DF2',
            fontFamily: Font.InterRegular400,
          }}>
          {data?.fileName}
        </TextCM>
        <View
          style={{
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'flex-start',
            columnGap: 4,
          }}>
          <TextCM
            style={{
              fontSize: 12,
              color: '#00204DF2',
              fontFamily: Font.InterRegular400,
            }}>
            {data?.sourceSystem || 'Từ eRequest'}
          </TextCM>
          <TextCM>•</TextCM>
          <TextCM
            style={{
              fontSize: 12,
              color: '#00204DF2',
              fontFamily: Font.InterRegular400,
            }}>
            {getSize(data?.fileSize)}
          </TextCM>
        </View>
        <View
          style={{
            borderRadius: 4,
            backgroundColor: '#E8F7EF',
            paddingHorizontal: 8,
            paddingVertical: 4,
            maxWidth: 140,
          }}>
          <TextCM
            style={{
              fontSize: 12,
              color: '#00204DF2',
              fontFamily: Font.InterRegular400,
              textAlign: 'center',
            }}>
            {getStatus(data?.status)}
          </TextCM>
        </View>
        <Divider />
      </View>
    </TouchableOpacity>
  );
};

export default ListItemField;
